import { Component, OnInit } from '@angular/core';

import { MenuService } from './service/menu-service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [MenuService]
})
export class AppComponent implements OnInit {
  title = 'OWF Angular Sample Widget<br>w/Prime Faces NG';

  constructor(private menuService: MenuService) {
    menuService.menuSubscriber$.subscribe(
      event => {
        let e: any = event;
        console.log(`${e.item.label} received by AppComponent`);
      }
    );
  }

  ngOnInit() { }

  notifyMenu() {
    this.menuService.publisherAction('New File');
    console.log('New File, pressed from AppComponent');
  }
}
